<template>
    <div class="post">
        <div v-if="loading" class="loading">
            Loading... 
        </div>
        <h1>Vendors</h1>
        <div v-if="post" class="content">
            <table class="grid">
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Created At</th>
                        <th>Approved?</th>
                        <th>Actions</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="vendor in post" :key="vendor.id">
                        <td>{{ vendor.storeName }}</td>
                        <td>{{ vendor.createdAt }}</td>
                        <td>{{ vendor.approved }}</td>
                        <td>
                            <span class="material-symbols-outlined" @click="edit(vendor.id)">
                                edit
                            </span>
                            <span class="material-symbols-outlined" @click="del(vendor.id)">
                               delete
                            </span>
                            <span class="material-symbols-outlined" v-if="!vendor.approved" @click="approve(vendor.id)">
                                thumb_up
                            </span>
                            <span class="material-symbols-outlined" @click="reject(vendor.id)">
                                thumb_down
                            </span>
                        </td>
                    </tr>
                </tbody>
            </table>
            
            <a class="skipnext" @click="prev()" v-if="pagenumber > 0">
                <span class="material-symbols-outlined">
                 skip_previous
                </span>
            </a>
            <a class="skipnext" @click="next()" v-if="count == paging">
                <span class="material-symbols-outlined">
                    skip_next
                </span>
            </a>
        </div>
    </div>
</template>

<script lang="js">
    import Vue from 'vue';
    import {DateTime} from 'luxon'
    import utils from '../utils';

    export default Vue.extend({
        data() {
            return {
                loading: false,
                post: null,
                pagenumber:0,
                count:0,
                paging: 20
            };
        },
        created() {
            // fetch the data when the view is created and the data is
            // already being observed
            this.fetchData();
        },
        watch: {
            // call again the method if the route changes
            '$route': 'fetchData'
        },
        methods: {
            async fetchData() {
                this.post = null;
                this.loading = true;
                var skip = this.pagenumber * this.paging

                await utils.getData(`vendor/search?skip=${skip}&take=${this.paging}`)
                    .then(json => {
                        this.post = json;
                        this.count = this.post.length;
                        this.post = this.post.map((p) => {
                            p.createdAt = DateTime.fromISO(p.createdAt).toLocaleString(DateTime.DATETIME_SHORT)
                            return p;
                        })
                        this.loading = false;
                        return;
                    });
            },
            edit(id){
                //figure out how to load an id into vendor registration
                window.location = `/#/vendor-registration?id=${id}`
            },
            async del(id){
                if (confirm('Are you sure?')){
                    await this.postData(`/vendor/delete?id=${id}`)
                    await this.fetchData();
                }
            },
            async approve(id){
                await utils.postData('/vendor/approve', {id, approved:true})
                await this.fetchData();
            },
            async reject(id){
                var denialReason = prompt("What reason would you like to give them for your rejection?")
                await this.postData('/vendor/approve', {id, approved:false, denialReason:denialReason})
                await this.fetchData();
            },
            next(){
                this.pagenumber++;
                this.fetchData();
            },
            prev(){
                this.pagenumber--;
                this.fetchData();
            }
        },
    });
</script>