<template>
    <div class="post">
         <h1>API Key</h1>
        <div id="api-holder">
            <input type="text" placeholder="API Key" v-model="apiKey" />
            <span class="material-symbols-outlined" @click="clipboard">
            content_copy
            </span>
            <span class="material-symbols-outlined" @click="genKey">
            recycling
            </span><br />
            {{ message }}
            <div class="error-holder"  v-if="error">
                {{ error }} 
                
                <span class="material-symbols-outlined cancel" @click="closeError">
                cancel
                </span>
            </div>
        </div>
       
    </div>
</template>

<script lang="js">
    import Vue from 'vue';
    import utils from '../utils'

    export default Vue.extend({
        data() {
            return {
                apiKey:"",
                message:"",
                error:""
            };
        },
        async created(){
            await this.getKey();
        },
        methods:{
            async getKey(){
                await utils.getData("/user/GetApiKey")
                .then(json => {
                    console.log(json)
                    this.apiKey = json.id
                });
            },
            async genKey(){
                this.message = "";
                if (confirm("Are you sure you want to regenerate an api key?  This will invalidate prior keys.")){
                    var resp = await this.postData("/user/GenApi")
                    console.log(resp)
                    this.apiKey = resp.value;
                }
            },
            clipboard(){
                this.message = "";
                if (this.apiKey){
                    navigator.clipboard.writeText(this.apiKey);
                    this.message="copied!"
                }else{
                    this.error="Must generate a key first!"
                }
            },
            closeError(){
                this.error = ""
            }
        }

    });
</script>
<style scoped>
  #api-holder{
    background: #EDE6D6 0% 0% no-repeat padding-box;
    border-radius: 10px;
    width: 686px;
    text-align: left;
    padding: 30px;
  }
  .cancel{
    float:right;
  }
</style>